<template>
	<div class="BAllBlog">
		<div class="allBlogContent">
			<v-article :articleList="articleList" :toEdit="toEdit" :isArticle="isArticle" @lastPage="lastPage" @firstPage="firstPage" @showMessage="showMessage"></v-article>
		</div>
	</div>
</template>

<script>
import Article from '@/components/common/article/article'
import { queryArticleList, currentUser } from '@/bmob'
export default {
	data() {
		return {
			articleList: [],
			toEdit: true,
      isArticle: true,
      userInfo: {}
		}
  },
  created () {
    this._initUserInfo()
  },
	methods: {
    _initUserInfo() {
      currentUser().then((result) => {
        this.userInfo = {
          'avatar': result.get('avatar'),
          'showName': result.get('showName'),
          'singName': result.get('singName'),
          'userId': result.id
        }
        this._getArticleList()
      }, (res) => {
        //session
				this.$router.push({ path: '/login' })
      })
		},
		_getArticleList() {
      queryArticleList({'userId': this.userInfo.userId}).then((result) => {
        this.articleList = result
        this.$emit('articleList', result)
      }, (res) => {
        this.$emit('showMessage', res)
      })
		},
		lastPage() {
			this.$emit('showMessage', '')
		},
		firstPage() {
			this.$emit('showMessage', '')
		},
		showMessage(msg) {
			this.$emit('showMessage', msg)
		}
	},
	components: {
		'v-article': Article
	}
}
</script>

<style lang="less" scoped>
.BAllBlog {
	position: fixed;
	top: 50px;
	bottom: 0;
	left: 200px;
	right: 0;
	background-color: #fff;
	overflow-y: scroll; // overflow-x: hidden;
	padding: 50px 15% 0 8%;
	.article {
		width: 1000px
	}
}
</style>



